.main_head{
    height: 100%;
    width: 90%;
    overflow-y: hidden;
    overflow-x: hidden;
    background-color: #292929;
    border-radius: 0 25px 0 0;
}

.top{
    padding: 5px;
    justify-content: space-around;
}
.mid .heading{
    color: white !important;
    font-size: x-large;
    text-align: center;
    align-content: center;
}
.active{
    /* background-color: #F30050; */

}
.address:hover{
    background-color: #F30050;
}
.main{
    flex-direction: row;
    width: auto;
    background: 
    linear-gradient(
        rgba(0, 0, 0, 0.5), 
        rgba(0, 0, 0, 0.5)
      ),
      url(./background.png);
      background-size: cover;
    background-repeat: no-repeat;
    justify-content: space-between;
    /* flex-direction: row; */
}
.support{
    flex: auto;
    /* float:right; */
    position:absolute; right:5px ; bottom: 10px;
}
.btn1{
    color: aliceblue;
    border-width: 2.5px;
    width: max-content;
    border-radius: 20px !important;
    border-color: white;
}
.card{
    background-color: #390554;
    color: white;
    padding: 10px;
    width: 15rem;
}
.content{
    color: white;
}
.foot{
    height: 40px;
    background-color: #F30050;
}

@media (max-width: 768px) {
    .main{
        flex-direction: row;
    }
    .main_head{
        background: none;
        height: auto;
        margin: 10px;
        flex-direction: row;
    }
}